import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { policyObjects } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    maxScores: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formPolicyObjects' })
class FormPolicyObjectsPage extends Component {
  state = {
    policyOjectId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ policyOjectId: match.params.id });
      this.getDepartmentID(match.params.id);
    }
  };

  getDepartmentID = code => {
    const policymap = policyObjects.filter(object => object.code === code)[0];
    const initialValue = handleInitialValue(policymap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { policyOjectId } = this.state;
    return (
      <Card title={policyOjectId ? 'Cập nhật đối tượng chính sách' : 'Thêm đối tượng chính sách'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Kí hiệu" {...formItemLayout}>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên đối tượng" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm tối đa" hasFeedback>
              {getFieldDecorator('maxScores', {
                initialValue: initialValue.maxScores,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#policy-objects/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPolicyObjectsPage {...props} />
  </Page>
));



